
body {
    padding: 0;
    margin: 0;
    font-size: .32rem;
    background: #f2f2f2;
}
ul,p {
    margin: 0;
    padding: 0;
}
*, *::before {
    box-sizing: border-box;
}
input, button {
    outline: none;
}
.container {
    padding-top: .4rem;
    margin: 0 .3rem;
}
.radius-small {
    border-radius: 6px;
    border: 2px solid #e8e8e8;
    overflow: hidden;
}
.radius-medium {
    border-radius: 12px;
    border: 2px solid #e8e8e8;
    overflow: hidden;
}
.radius-big {
    border-radius: .16rem;
    border: 2px solid #e8e8e8;
}
.com-title {
    font-size: .38rem;
        color: #222;
}
.com-tips {
    font-size: .22rem;
    color: #666;
}
.windowwidthhr{
    margin: 0 -.3rem;
    border: 0;
    border-top: 1px solid #d7d7d7;
}
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
}



